.object-collection-view-sort-options {
    display: flex;
    align-items: center;
    padding-top: 1px;
}

.select-all {
    padding-left: 16px;
}

.reverse-order-menuitem {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
}

.reverse-order-menuitem-text {
    flex-grow: 1;
}

.reverse-order-menuitem-arrow {
    flex-grow: 0;
    flex-shrink: 0;
    margin-left: 16px;
}

.sort-options-compact {
    padding: 2px 8px;
}