.formula-TagList-clearButton {
    height: 24px;
    padding: 0 5px;
    margin: 4px;
}

.formula-TagList-clearButton span {
    margin-left: 4px !important
}

.formula-TagList.spectrum-Tags {
    display: inline-flex;
}

.formula-ExpandableTag {
    display: flex;
    align-items: center;
}

.formula-ExpandableTag-label {
    display: flex;
    align-items: center;
    padding-right: 8px;
}

.formula-ExpandableTag-icon {
    padding: 8px;
}

.formula-ExpandableTag-expand {
    cursor: pointer;
}
