
.layout-table, .layout-list {
    height: 100%;
    width: 100%;
    min-height: 250px;
}

.layout-grid {
    height: calc(100% - 35px); /* TODO only subtract if sort options are there (eg not for compact) */
    width: 100%;
    min-height: 250px;
}

.layout-list .spectrum-Table-head {
    display: none; /* hide header row */
}

.layout-list-cell {
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
    align-items: center;
}

.layout-list-cell-preview {
    width: 60px;
    height: calc(100% - 4px);
    flex-shrink: 0;
}

.layout-list-cell-preview + .layout-list-cell-body {
    padding-left: 14px;
}

.layout-list-cell-body {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.layout-list-cell-title,
.layout-list-cell-subtitle {
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 8px;
}

.layout-list-cell-title {
    font-size: 14px;
    color: rgb(75, 75, 75);
}

.layout-list-cell-subtitle {
    font-size: 11px;
    letter-spacing: 0.06em;
    color: rgb(110, 110, 110);
}

.layout-list-cell-quickActions {
    position: absolute;
    right: 0;
}