.filter-bar {
    display: flex;
}

.filter-bar-search {
    width: 100%;
}

.filter-bar-search > input {
    width: 100%;
}

.filter-bar > .filter-button {
    flex-grow: 0;
    flex-shrink: 0;
}

.filter-button > .is-selected {
    background-color: rgb(225, 225, 225);
}

.filter-modal .spectrum-Dialog-content {
    height: 420px;
    width: 400px;
    overflow-y: scroll;
}

.filters-tags.formula-TagList {
    display: inline-block;
}

.filters-compact {
    padding: 2px 8px;
}

.filters-compact .filters-search {
    flex-grow: 1;
    padding-right: 4px;
}

.filters {
    padding: 2px 8px;
}