.object-picker {
    height: 100%;
}

.object-picker .object-picker-left-rail {
    width: 0;
    flex-shrink: 0;
    flex-grow: 0;
    overflow: hidden;
    /*transition: width 500ms linear 0s;*/
}

.object-picker .object-picker-left-rail.is-open {
    width: 250px;
    /*transition: width 100ms linear 0s;*/
}

.object-picker-selection-compact {
    height: 32px;
}

.object-picker-selection-count {
    padding-right: 14px;
    font-size: 14px;
    color: rgb(20, 115, 230);
}

.object-picker-selection-count.no-selection {
    font-style: italic;
    color: rgb(44, 44, 44);
}

